.cursor {
  position: fixed;
  left: 0;
  top: 0;
  width: 30px; /* Smaller size */
  height: 30px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.07); /* More subtle */
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
  transform: translate(-50%, -50%);
}

.cursor-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px; /* Smaller inner circle */
  height: 6px;
  background-color: white;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.1s ease-out;
}

.cursor.clicked {
  background-color: rgba(255, 107, 107, 0.2); /* More subtle */
}

.cursor.hovered {
  background-color: rgba(255, 107, 107, 0.1); /* More subtle */
  mix-blend-mode: normal;
}

.cursor.hidden {
  opacity: 0;
}

/* IMPORTANT: Don't hide the default cursor! */
/* Instead, just let the custom cursor complement it */
/* 
body {
  cursor: none;
}
*/

/* Keep default cursor on interactive elements for better usability */
input, 
textarea, 
select, 
option,
a,
button {
  cursor: pointer; /* Show pointer on interactive elements */
}

/* Hide custom cursor on mobile */
@media (max-width: 768px) {
  .cursor {
    display: none;
  }
}